import React, { useState } from 'react';
import { validateEmail } from '../../utils/helpers';

function ContactForm() {
        // sets form to empty strings, clearing unput fields 
    // formState has 3 keyvalue pairs to represent inputs
    const [formState, setFormState] = useState({ name:'', email: '', message: ''});
    const [errorMessage, setErrorMessage] = useState('');
    const { name, email, message } = formState;

    function handleChange(e) {
        if (e.target.name === 'email') {
            const isValid = validateEmail(e.target.value);
            console.log(isValid);
            // isValid conditional statement
            if (!isValid) {
                setErrorMessage('Your email is invalid.');
              } else {
                setErrorMessage('');
                }
            } else {
                if (!e.target.value.length) {
                  setErrorMessage(`${e.target.name} is required.`);
                } else {
                  setErrorMessage('');
                }
              
          }  
        //   only allows the state to update with the user input if no error messages
          if (!errorMessage) {
              setFormState({ ...formState, [e.target.name]: e.target.value });
          }
        // uses spread operator in order to retain other key-value pairs in the object
        setFormState({...formState, [e.target.name]: e.target.value })
       
    }
    // console.log outside of handleChange, but inside contactForm()
    // will console log every change in the input fields
    // console.log(formState);
    function handleSubmit(e) {
        e.preventDefault();
        console.log(formState);
    }

    return (
        <section>
            <h1 data-testid="h1tag">Contact Me</h1>
            <form id="contact-form" onSubmit={handleSubmit}>
                <div>
                    <label htmlFor="name">Name:</label>
                    <input type="text" defaultValue={name} onBlur={handleChange} name="name" />
                </div>
                <div>
                    <label htmlFor="email">Email address:</label>
                    <input type="email" name="email" defaultValue={email} onBlur={handleChange}/>
                </div>
                <div>
                    <label htmlFor="message">Message:</label>
                    <textarea name="message" defaultValue={message} onBlur={handleChange} rows="5"  />
                </div>
                {/* error message appears if the errorMessage contains an error message */}
                {errorMessage && (
                    <div>
                        <p className="error-text">{errorMessage}</p>
                    </div>
                )}
                <button data-testid="button" type="submit">Submit</button>
            </form>
        </section>
    );
}
    
export default ContactForm;